<template>
  <div class="row">
    <modal :show="modalStatus">
      <h6 class="modal-title" id="modal-title-default">Reset Password</h6>

      <p class="mt-4">In order to update this admin password, please be careful</p>

      <form @submit.prevent="">
        <div class="d-flex flex-column">
          <input-component
            @input="password"
            type="password"
            placeholder="Your Password"
            name="password"
            size="md"
            :value="form.currentPassword"
          />

          <input-component
            @input="newPassword"
            type="text"
            placeholder="New Password For Admin"
            name="password"
            size="md"
            :value="form.newPassword"
            :valid="formValidation.newPassword.isValid"
            :startValidate="formValidation.newPassword.startValidate"
            :errorMessage="formValidation.newPassword.errorMessage"
          />

          <input-component
            @input="passwordConfirmation"
            type="text"
            placeholder="Confirm New Password"
            name="password"
            size="md"
            :value="form.newPasswordConfirmation"
            :valid="formValidation.newPasswordConfirmation.isValid"
            :startValidate="formValidation.newPasswordConfirmation.startValidate"
            :errorMessage="formValidation.newPasswordConfirmation.errorMessage"
          />
        </div>

        <div class="d-flex justify-content-between gap-1">
          <button-component
            class="mt-4"
            variant="gradient"
            color="success"
            size="sm"
            :isLoading="actionInProgress.loading"
            :disabled="actionInProgress.loading || formStatus"
            @click="resetPassword()"
          >
            Submit
          </button-component>
          <button-component class="mt-4" variant="gradient" color="danger" size="sm" @click="toggleModal()">
            Close
          </button-component>
        </div>
      </form>
    </modal>
  </div>
</template>

<script>
  import Modal from '@/components/Modal-dialog';
  import ButtonComponent from '@/components/Button.vue';
  import InputComponent from '@/components/Input.vue';

  export default {
    name: 'add-admin',
    components: { Modal, ButtonComponent, InputComponent },
    data() {
      return {
        form: {
          adminId: '',
          currentPassword: '',
          newPassword: '',
          newPasswordConfirmation: '',
        },
        formValidation: {
          newPassword: { isValid: false, startValidate: false, errorMessage: '' },
          newPasswordConfirmation: { isValid: false, startValidate: false, errorMessage: '' },
        },
      };
    },
    computed: {
      modalStatus() {
        return this.$store.state.AdminsStore.modals.resetPasswordAdmin;
      },
      actionInProgress() {
        return this.$store.state.AdminsStore.progress.adminResetPassword;
      },
      formStatus() {
        return this.formValidation.newPassword.isValid && this.formValidation.newPasswordConfirmation.isValid;
      },
      adminId() {
        return this.$store.state.AdminsStore.adminId;
      },
    },

    methods: {
      password(event) {
        this.form.currentPassword = event.target.value;
      },
      newPassword(event) {
        this.form.newPassword = event.target.value;
      },
      passwordConfirmation(event) {
        this.form.newPasswordConfirmation = event.target.value;
      },
      toggleModal() {
        this.$store.commit('AdminsStore/TOGGLE_RESET_PASSWORD_ADMIN_MODAL');
      },
      startValidation() {
        this.formValidation.newPassword.startValidate = true;
        this.formValidation.newPasswordConfirmation.startValidate = true;
      },
      checkForm() {
        this.startValidation();
        const passwordValidationRegex = new RegExp(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d).{8,}$/);
        if (passwordValidationRegex.test(this.form.newPassword)) {
          this.formValidation.newPassword.isValid = true;
        } else {
          this.formValidation.newPassword.isValid = false;
          this.formValidation.newPassword.errorMessage =
            'Password must contain at least 8 character and at least 1 lower case letter and 1 one capital case letter';
        }
        if (this.form.newPasswordConfirmation === this.form.newPassword) {
          this.formValidation.newPasswordConfirmation.isValid = true;
        } else {
          this.formValidation.newPasswordConfirmation.isValid = false;
          this.formValidation.newPasswordConfirmation.errorMessage = "passwords isn't match";
        }
      },
      resetPassword() {
        this.checkForm();
        if (this.formStatus) {
          const FormData = {
            currentPassword: this.form.currentPassword,
            newPassword: this.form.newPassword,
            id: this.adminId,
          };
          this.$store.dispatch('AdminsStore/resetPasswordForAdmin', FormData);
        }
      },
    },
  };
</script>
